.body-form{
    width: 100%;
    height: auto;
    margin: 50px 0px 0px 0px;
    display: flex;
    justify-content: center;
}

.content{
    width: 30%;
    display: block;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5) inset;
    border-radius: 5px;
}

.body-form .content .c-image img{    
    width: 50px;
    margin: 20px 0px;
}

.c-title{
    font-size: 30px;
    color: blue;
    font-weight: bold;
    margin-bottom: 30px;
}

.c-form{
    width: 100%;
    justify-content: center;
}

.label-title{
    width: 100%;
    display: flex;
    justify-content: flex-start;
}

.label-title label{
    margin: 0px 0px 5px 5%;

}

.input-e, .input-p{
    width: 90%;
    margin: 2px 0px 30px 0px;
    height: 40px;
    border-radius: 5px;
    border:none;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3) inset;
}

.forgotPass{
    width: 100%;
    display: flex;
    justify-content: flex-end; 
}

.forgotPass a{
    text-align: right;
    color: gray;
    margin-right: 20px;
    text-decoration: none;
}

.forgotPass a:hover{
    color: blue;
    text-decoration: none;
    background-color: none;
    background: none;
}

.btnLogin{
    width: 300px;
    padding: 12px;
    margin: 20px 0px 40px 0px;
    border-radius: 5px;
    background-color: blue;
    color: #ffff;
    font-size: 15px;
    font-weight: 550;
}

.msg_error{
    
    width: 200px;
    background-color: rgba(255, 1, 1, 0.205);
    padding: 12px 50px;
    margin: auto;
    margin-bottom: 20px;
    font-size: 15px;    
    border-radius: 5px;
}

@media screen and (max-width:900px) {
  
    .content{
        width: 70%;
        min-width: 250px;
    }    
}

@media screen and (max-width:450px) {
    .btnLogin{
        width: 200px;
    }
    
    .msg_error{
        padding: 10px;
        width: 180px;
    }
}