.header{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    background-color: rgb(3, 3, 94);
}

.header-c{
    width: 80%;
    height: auto;
    display: flex;    
    color: #ffff;
}

.header .header-c img{
    width: 100px; 
    margin: -135px 0px;
    align-items: flex-start;
    text-align: left;
    height: auto;
}

.header-c_nav{    
    width: 80%;
    height: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;  
}

ul{
    display: flex;
    justify-content: center;
    height: auto; 
}

li{
    list-style: none;
    height: auto;
    display: block;
    align-self: auto;
    justify-content: center; 
}

nav ul li a{
    width: auto; 
    padding: 25px 10px;
    color: #ffff;
    text-decoration: none;  
    height: auto;  
    margin-left: 40px;
    text-align: center; 
    font-family: 'Catamaran', sans-serif;    
}

nav ul li a:hover{
    background-color: rgb(2, 2, 61);    
}

@media screen and (max-width:800px) {

    .header-c{
        width: 95%;        
    }
    
    .header .header-c img{
        width: 100px; 
        margin: -100px 0px -100px 0px;
    }
    
    .header-c_nav{    
        width: 90%;  
    }
    
    a{
        padding: 36px;    
    }
    
    a:hover{
        background-color: rgb(2, 2, 61);
        padding: auto;    
    }    

}